import React, { useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import clsx from 'clsx';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Collapse from '@material-ui/core/Collapse';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import { red } from '@material-ui/core/colors';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import config from "./../../config";
import { listMaintenances } from '../../api/api';

const useStyles = makeStyles((theme) => ({
    root: {
        maxWidth: 345,
    },
    media: {
        height: 0,
        paddingTop: '56.25%', // 16:9
    },
    expand: {
        transform: 'rotate(0deg)',
        marginLeft: 'auto',
        transition: theme.transitions.create('transform', {
            duration: theme.transitions.duration.shortest,
        }),
    },
    expandOpen: {
        transform: 'rotate(180deg)',
    },
    avatar: {
        backgroundColor: red[500],
    },
}));

const Home = _ => {

    const [totalMaintenances, setTotalMaintenances] = useState(0)

    useEffect(() => {

        let data = { paginate: false, "source": "", "downlFoaded": true }
        listMaintenances(data).then(response => {
            setTotalMaintenances(response.count);
        });
    }, []);


    const onGoToMaintenanceDownloaded = () => {
        window.location.href = "/maintenances-downloaded"
    }

    const classes = useStyles();


    return (
        <div className='work' onClick={onGoToMaintenanceDownloaded}>
            {
                totalMaintenances > 0 ?
                    <Card className={classes.root}>
                        <CardHeader
                            avatar={
                                <Avatar aria-label="recipe" className={classes.avatar}>
                                    {totalMaintenances}
                                </Avatar>
                            }
                            action={
                                <IconButton aria-label="settings">
                                    <MoreVertIcon />
                                </IconButton>
                            }
                            title="Mantenimientos pendientes"
                            subheader=""
                        />
                        <CardMedia
                            className={classes.media}
                            image={config.host + "work.svg"}
                            title="Mantenimientos pendiente"
                        />
                        <CardContent>
                            <Typography variant="body2" color="textSecondary" component="p">
                                Tienes {totalMaintenances} mantinimientos descargados click en esta notificaci√≥n para ver detalle
                            </Typography>
                        </CardContent>


                    </Card> : ""
            }
        </div>
    );
}
export default Home